.menu {
  font-size: 1.2em;
  margin: 10px 7% 0 7%;
  @include justifyBetween;
  align-items: center;
  @include devices(small-pc) {
    font-size: 0.8rem;
    margin: 15px 7% 20px 7%;
    text-transform: uppercase;
  }
  @include devices(mobile) {
    @include marge-page-mobile;
  }
  ul {
    display: flex;
    flex-direction: row;
    padding: 0 0 0 0;
    @include devices(small-pc) {
      margin: 0;
    }
  }
  a {
    text-decoration: none;
    font-weight: bold;
  }
  li:nth-child(1) {
    margin-right: 40px;
    @include devices(small-pc) {
      margin-right: 20px;
    }
  }
  .activeLink {
    text-decoration: underline;
  }
  .box_logo {
    display: block;
    @include devices(small-pc) {
      width: 160px;
      height: auto;
    }
    .header_logo {
      &:after {
        @include logo;
        background-color: $bg-logoMenu;
        @include devices(small-mobile) {
          transform: scale(0.8);
          text-align: left;
          margin: 0;
        }
      }
    }
  }
}
